/* 自定义resets */
a {
  text-decoration: none;
}
ul {
  margin: 0;
  padding: 0;
}
li {
  list-style: none;
}
h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}
html {
  height: 100%;
}
body {
  height: 100%;
  width: 100%;
  font-size: 14px;
  line-height: 1;
  color: #313131;
  overflow-x: hidden;
}
.clear, #app, #content, .article {
  zoom: 1;
}
.clear:after, #app:after, #content:after, .article:after {
    content: '';
    display: table;
    clear: both;
}
.clear:before, #app:before, #content:before, .article:before {
    content: '';
    display: table;
}
#app {
  height: 100%;
}
.break, .post__title {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
