#hd {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 46px;
  border-bottom: 2px solid #e8e8e8;
  background-color: #fff;
  -webkit-transition: all .5s;
  transition: all .5s;
  /* 右移显示菜单 */
}
#hd.open {
    -webkit-transition: all .5s;
    transition: all .5s;
    left: 200px;
}
.nv-bar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 36px;
  padding: 5px 10px;
}
.nv-bar__menu-btn {
    width: 19px;
    height: 16px;
    background: url(data:image/png;base64,) no-repeat center;
    background-size: 19px 16px;
}
.nv-bar__txt {
    font-size: 16px;
}
.nv-bar__add .add-icon {
    border-radius: 5px;
    color: #42b983;
}
.page-cover {
  position: fixed;
  z-index: 2;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(20, 20, 20, 0.7);
}
#side-bar {
  position: absolute;
  top: 0;
  left: -200px;
  z-index: 3;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column nowrap;
          flex-flow: column nowrap;
  width: 200px;
  height: 1000px;
  background-color: #fff;
}
.menu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column nowrap;
          flex-flow: column nowrap;
  border-top: 1px solid #d5dbdb;
  margin: 0 25px 0 25px;
  padding-top: 20px;
  font-size: 14px;
  font-weight: 700;
  line-height: 3;
}
.menu__item {
    color: #313131;
}
.msg-about-box {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column nowrap;
          flex-flow: column nowrap;
  border-top: 1px solid #d5dbdb;
  margin: 0 25px 0 25px;
  font-size: 14px;
  font-weight: 700;
  line-height: 3;
}
.msg-about-box .message {
    color: #313131;
}
.msg-about-box .about {
    color: #313131;
}
#topics {
  margin-top: 48px;
}
/* 提示加载中 */
.loading {
  width: 100%;
  height: 100px;
  margin-top: 202px;
  background: url(data:image/gif;base64,) no-repeat center top;
}
/* 每个post的样式 */
.post__item {
  padding: 5px 10px;
  border-bottom: 1px solid #d4d4d4;
}
.post__info {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-top: 10px;
  line-height: 1.5;
}
.post__author {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.post__author .avatar {
    width: 40px;
    height: 40px;
    margin-right: 1em;
    border: 1px solid #f3f3f3;
    border-radius: 50%;
}
.post__tag {
  display: inline-block;
  width: 3em;
  line-height: 2em;
  margin-right: 1em;
  text-align: center;
  border-radius: 5px;
  font-size: 14px;
  font-weight: normal;
  color: #ffffff;
  vertical-align: 1px;
  /* 背景颜色 */
}
.post__tag.share {
    background: #42B983;
}
.post__tag.ask {
    background: #3498DB;
}
.post__tag.job {
    background: #9B59B6;
}
.post__tag.good {
    background: #E67E22;
}
.post__tag.top {
    background: #E74C3C;
}
.post__title {
  font-size: 16px;
  font-weight: bold;
  color: #2c3e50;
}
.post__info {
  color: #34495e;
}
.post__other {
  text-align: right;
}
.post .reply-count {
  color: #42B983;
  font-weight: bold;
}
#about {
  height: 100%;
  padding-top: 48px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.about-info {
  height: 100%;
  padding: 10px 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: #F7F7F7;
}
.about-info h3 {
    line-height: 3;
}
.about-info p, .about-info a {
    line-height: 2;
}
.desc, .address {
  border-bottom: 1px solid #dbdbd5;
  margin-bottom: 10px;
}
#content {
  padding-top: 48px;
}

.icon-top {
  position: fixed;
  right: 10px;
  bottom: 80px;
  font-size: 50px;
  z-index: 999;
  color: #42b983;
}
